@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.test {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
  background: #96acbe;
}

.intro-container {
	max-width: 1000px;
	min-height: 300px;
	border-radius: 20px;
	border-bottom: solid #6d4232 50px;
	padding: 30px;
	margin: 10px;
	background: #8bccbf;
	display: grid;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-gap: 10px;

	grid-template-columns: 1fr;
	grid-template-rows: auto auto;

	grid-template-areas:
		"img"
		"desc"
		"skills";
	
}

@media (min-width: 600px) {
	.intro-container {
		grid-template-columns: 1fr;

		grid-template-areas: 
		"desc img"
		"desc skills";
	}
}

.intro-desc {
	font-size: 1rem;
	grid-area: desc;
}

.intro-img {
	background: thistle;
	grid-area: img;
}

.intro-skills {
	background: yellowgreen;
	grid-area: skills;
}
.navbar {
	background-color: #15755a;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: start;
	        justify-content: start;
	-webkit-align-items: center;
	        align-items: center;
  }
  
  .menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
	color: #c5dde1;
  }
  
  .nav-menu {
	background-color: #15755a;
	border-right: solid #c5dde1 10px;
	border-radius: 0px 30px 30px 0px;
	width: 250px;
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
  }
  
  .nav-menu.active {
	left: 0;
	transition: 350ms;
  }
  
  .nav-text {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: start;
	        justify-content: start;
	-webkit-align-items: center;
	        align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
  }
  
  .nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 0 16px;
	border-radius: 4px;
  }
  
  .nav-text a:hover {
	background-color: #7e899f;
  }
  
  .nav-menu-items {
	width: 100%;
  }
  
  .navbar-toggle {
	background-color: #15755a;
	width: 100%;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: start;
	        justify-content: start;
	-webkit-align-items: center;
	        align-items: center;
  }
  
  span {
	margin-left: 16px;
  }
